.card {
    background-color: black;
    border: 2px solid orangered;
    border-radius: 0.6rem;
    -webkit-box-shadow: 0px 11px 35px -4px white;
            box-shadow: 0px 11px 35px -4px white;
    color: white;
    float: left;
    font-family: 'Creepster', cursive;
    font-size: 15pt;
     opacity: 0.85;
    margin-right: 20px;
    margin-bottom: 20px;
    padding: 5px;
    width: 20%;
}

.card-body {
    text-align: center;
}

.card-img-top {
    border-radius: 0.6rem;
    height: 200px;
    margin: 0 auto;
    overflow: hidden;
    width: 200px;
}

.card-title {
    font-family: 'Creepster', cursive;
}

.custom {
  cursor: url(icons8-jack-o\'-lantern-48.png), auto;
}


